var Mongoose = require('mongoose');
var DataB = require('./SchemaM');

const FindDATA = ({ fetchedUser }) => {
  DataB.findOne({
    userID: fetchedUser.id
  },(data, err) => {
    if(err) console.log(err);
    if(!data){
      const nData = new DataB({
        userID: fetchedUser,
        history: 0,
        historypday: 2,
      })
      nData.save().catch(err => console.log(err));
    }
  })
}
